@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input,textarea,button,select,a{-webkit-tap-highlight-color:rgba(0,0,0,0)}#drum-machine{text-align:center;background-color:#000000;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}#drum-machine #bank-display{margin:5px}#drum-machine #slider-container{margin:5px}#drum-machine #drumpad-container{width:70vw;height:50vh;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(3, 1fr)}#drum-machine #drumpad-container .drum-pad{font-family:'Sora', sans-serif;background-color:#3f3f3f;font-size:0.6em;color:#fff;border-radius:5px;border:none;cursor:pointer;padding:10px;margin:3px}#drum-machine #drumpad-container .drum-pad:hover{opacity:0.9}#drum-machine #switch-bank-btn{font-family:'Sora', sans-serif;background-color:#3f3f3f;font-size:0.6em;color:#fff;border-radius:5px;border:none;cursor:pointer;padding:10px;margin:5px}#drum-machine #switch-bank-btn:hover{opacity:0.9}

